import { classLogout, classLogin, appSign, fetch } from './api';

export default class Auth {
    public static classLogin(user_id: string, password: string): Promise<any> {
        return fetch.post(classLogin, {
            user_id,
            password,
        });
    }

    public static classLogout(user_id: string): Promise<any> {
        return fetch.post(classLogout, {
            user_id,
        });
    }

    public static appSign(user_id: string): Promise<any> {
        return fetch.get(appSign, {
            params: { user_id },
        });
    }
}
