    copyPassword(passWord: string) {
        let clipboard = new ClipboardJS('.sharePassword', {
            text() {
                return passWord;
            },
        });
        clipboard.on('success', () => {
            this.$message('直播密码已复制');
        });
        clipboard.on('error', () => {
            this.$message.error('复制到粘贴板失败，手动复制一下地址吧:' + passWord);
        });
    }
    copyStr(className: string, str: string) {
        let clipboard = new ClipboardJS(className, {
            text() {
                const urlSp = new URLSearchParams(new URL(str).search);
                return str.replace(/\?.+/, '?' + urlSp.toString());
            },
        });
        clipboard.on('success', () => {
            this.$message({ message: '成功复制到粘贴板，快去分享吧', type: 'success' });
        });
        clipboard.on('error', () => {
            this.$message.error('复制到粘贴板失败，手动复制一下地址吧:' + str);
        });
    }
