        el.validate((valiRes: any) => {
            if (valiRes) {
                // 登录过的话就是更新appId
                if (this.auths.appId) {
                    this.appUpdateId();
                } else {
                    this.appAddId();
                }
            }
        });
    }

    public updateTitle() {
        Room.classTitle(this.auths.user_id, this.form.componyName).then((res: any) => {
            if (res.ret.code == 200) {
                if (res.msg && res.msg.code === 200) {
                    this.title = res.title;
                    this.auths.title = res.title;
                    LocalStorage.update('auth', this.auths);
                    this.getRoom();
                } else {
                    this.$notify.error({
                        title: '错误',
                        message: '请重新登录',
                    });
                    this.router.push({ path: '/login/' });
                }
            }
        });
    }

    public appAddId() {
        let appId = Number(this.appForm.appId);
        Room.appAdd(this.auths.user_id, appId, this.appForm.appSign).then((res: any) => {
            if (res.ret.code == 200) {
                if (res.msg && res.msg.code === 200) {
                    this.appDialogFormVisible = false; // 请求成功后才准关闭
                    // this.title = res.title;
                    // this.getRoom();
                    this.auths.appId = this.appForm.appId;
                    LocalStorage.update('auth', this.auths);
                } else {
                    this.$notify.error({
                        title: '错误',
                        message: '请填写正确的appId',
                    });
                }
            } else {
                this.$notify.error({
                    title: '错误',
                    message: '服务器报错',
                });
            }
        });
    }

    public appUpdateId() {
        // this.auths.appId = this.appForm.appId;
        // LocalStorage.update('auth', this.auths);
        let appId = Number(this.appForm.appId);
        Room.appModify(this.auths.user_id, appId, this.appForm.appSign).then((res: any) => {
            if (res.ret.code == 200) {
                if (res.msg && res.msg.code === 200) {
                    this.auths.appId = this.appForm.appId;
                    LocalStorage.update('auth', this.auths);
                    this.appDialogFormVisible = false; // 请求成功后才准关闭
                    this.$message({
                        message: '修改成功，请重新复制链接',
                        type: 'success',
                    });
                    // this.title = res.title;
                    // this.getRoom();
                } else {
                    this.appDialogFormVisible = false;
                    this.$notify.error({
                        title: '错误',
                        message: '更新失败',
                    });
                }
            } else {
                this.$notify.error({
                    title: '错误',
                    message: '服务器报错',
                });
            }
        });
    }

    public updateRoom(data: any) {
        Room.classModify(
            data.password,
            data.className,
            this.auths.user_id,
            data.startTime,
            data.teacher,
            data.roomId,
        ).then((res: any) => {
            if (res.ret.code == 200) {
                if (res.msg && res.msg.code === 200) {
                    this.getRoom();
                } else {
                    this.$notify.error({
                        title: '错误',
                        message: '请重新登录',
                    });
                    this.router.push({ path: '/login/' });
                }
            }
        });
    }

    public getRoom() {
        Room.classRoomlist(this.auths.user_id)
            .then((res: any) => {
                if (res.ret.code == 200) {
                    if (res.msg && res.msg.code === 200) {
                        if (res.room_list.length !== 0) {
                            this.roomData = res.room_list;
                            this.showTip = false;
                        } else {
                            this.roomData = [];
                            this.showTip = true;
                        }
                    } else {
                        this.$notify.error({
                            title: '错误',
                            message: '请重新登录',
                        });
                        this.router.push({ path: '/login/' });
                    }
                }
            })
            .catch(error => {
                this.$message.error('请重新登录');
                this.router.push({ path: '/login/' });
            });
    }

    public creatRoom(data: any): void {
        Room.classCreate(data.password, data.className, this.auths.user_id, data.startTime, data.teacher).then(
            (res: any) => {
                if (res.msg && res.msg.code === 200) {
                    this.getRoom();
                } else {
                    this.$message.error('创建房间失败');
                }
            },
        );
    }

    public delRoom(data: any): void {
        Room.classDelete(data.room_id, this.auths.user_id)
            .then((res: any) => {
                if (res.ret.code == 200) {
                    if (res.msg && res.msg.code === 200) {
                        this.getRoom();
                    } else {
                        this.$message.error('删除房间失败');
                    }
                }
            })
            .catch(error => {
                this.$message.error('请重新登录');
                this.router.push({ path: '/login/' });
            });
    }

