    public outLogin(): void {
        Auth.classLogout(this.auths.user_id).then((res: any) => {
            if (res.msg && res.msg.code === 200) {
                LocalStorage.clear('auth');
                this.router.push({ path: '/login/' });
            } else {
                this.$message.error('登出失败');
            }
        });
    }

    public appSign(): void {
        Auth.appSign(this.auths.user_id).then((res: any) => {
            if (res.msg && res.msg.code === 200) {
                this.appForm.appId = res.app_id;
                this.appForm.appSign = res.app_sign;
            } else {
                this.$message.error('查询appId失败');
            }
        });
    }

    private nameValid(rule: any, value: string, callback: (err?: Error) => void) {
        if (value) {
            if (!this.checkSpeciaKey(value)) {
                callback(new Error('仅支持输入中英文、数字。'));
            } else {
                callback();
            }
        } else {
            callback();
        }
    }

    private appIdValid(rule: any, value: any, callback: (err?: Error) => void) {
        if (value) {
            if (!this.checkNumber(value)) {
                callback(new Error('仅支持输入数字。'));
            } else {
                callback();
            }
        } else {
            callback();
        }
    }

    checkSpeciaKey(str: string) {
        let specialKey = "[`~!@#$^&*()=|{}':;',\\[\\].<>/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？@";
        for (let i = 0; i < str.length; i++) {
            if (specialKey.indexOf(str.substr(i, 1)) != -1) {
                return false;
            }
        }
        return true;
    }

    checkNumber(str: string) {
        const reg = /^\d+$/;
        console.log(reg.test(str));
        return reg.test(str);
    }

    mounted() {
        this.router = this.$router as VueRouter;
        this.auths = LocalStorage.get('auth');
        if (this.auths.title) {
