import {
    classRoomlist,
    classCreate,
    classModify,
    classDelete,
    classTitle,
    classRoom,
    appAdd,
    appModify,
    flowLimit,
    fetch,
} from './api';

export default class Room {
    public static classCreate(
        room_key: string,
        room_name: string,
        user_id: string,
        start_time: string,
        anchor_name: string,
    ): Promise<any> {
        return fetch.post(classCreate, {
            room_name,
            room_key,
            user_id,
            start_time,
            anchor_name,
        });
    }

    public static classRoomlist(user_id: string): Promise<any> {
        return fetch.get(classRoomlist, {
            params: { user_id },
        });
    }

    public static classModify(
        room_key: string,
        room_name: string,
        user_id: string,
        start_time: string,
        anchor_name: string,
        room_id: string,
    ): Promise<any> {
        return fetch.post(classModify, {
            room_name,
            room_key,
            user_id,
            start_time,
            anchor_name,
            room_id,
        });
    }

    public static classTitle(user_id: string, title: string): Promise<any> {
        return fetch.post(classTitle, {
            user_id,
            title,
        });
    }

    public static classRoom(room_id: string): Promise<any> {
        return fetch.get(classRoom, {
            params: { room_id },
        });
    }

    public static classDelete(room_id: string, user_id: string): Promise<any> {
        return fetch.post(classDelete, {
            room_id,
            user_id,
        });
    }

    public static appAdd(user_id: string, app_id: number, app_sign: string): Promise<any> {
        return fetch.post(appAdd, {
            app_id,
            user_id,
            app_sign,
        });
    }

    public static appModify(user_id: string, app_id: number, app_sign: string): Promise<any> {
        return fetch.post(appModify, {
            app_id,
            user_id,
            app_sign,
        });
    }

    // 流量查询
    public static flowLimit(user_id: string, app_id: number): Promise<any> {
        return fetch.post(flowLimit, {
            app_id,
            user_id,
        });
    }
}
