




































import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({
    props: {
        roomDialog: Object,
    },
})
export default class RoomAdd extends Vue {
    @Prop() public roomDialog: any;

    formLabelWidth = '120px';
    roomForm = {
        className: '',
        teacher: '',
        startTime: '',
        password: '',
    };
    rules = {
        className: [
            { required: true, message: '请输入课堂名称', trigger: 'blur' },
            { trigger: 'blur', validator: this.nameValid },
        ],
        teacher: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { trigger: 'blur', validator: this.nameValid },
        ],
        password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { trigger: 'blur', validator: this.characterValid },
        ],
    };
    // method
    public addRoomBtn(roomForm: any) {
        let addData = {};
        const el: any = this.$refs[roomForm];
        el.validate((valiRes: any) => {
            if (valiRes) {
                let addData = {};
                addData = Object.assign({}, this.roomForm);
                this.$emit('insert', addData);
                el.resetFields();
                this.roomDialog.show = false;
            }
        });
    }

    private nameValid(rule: any, value: string, callback: (err?: Error) => void) {
        if (value) {
            if (!this.checkSpeciaKey(value)) {
                callback(new Error('仅支持输入中英文、数字。'));
            } else {
                callback();
            }
        } else {
            callback();
        }
    }

    private characterValid(rule: any, value: string, callback: (err?: Error) => void) {
        if (value) {
            if (!this.checkCharacter(value)) {
                callback(new Error('密码不支持中文'));
            } else {
                callback();
            }
        } else {
            callback();
        }
    }

    checkSpeciaKey(str: string) {
        let specialKey = "[`~!@#$^&*()=|{}':;',\\[\\].<>/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？@";
        for (let i = 0; i < str.length; i++) {
            if (specialKey.indexOf(str.substr(i, 1)) != -1) {
                return false;
            }
        }
        return true;
    }

    checkCharacter(str: string) {
        let re = new RegExp('[\\u4E00-\\u9FFF]+', 'g');
        if (re.test(str)) {
            return false;
        }
        return true;
    }
}
