




































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';

@Component({
    props: {
        roomDialogEdit: Object,
        roomDialogEditData: Object,
    },
})
export default class RoomEdit extends Vue {
    @Prop() public roomDialogEdit: any;
    @Prop() public roomDialogEditData: any;

    formLabelWidth = '120px';
    roomForm = {
        className: '',
        teacher: '',
        startTime: '',
        password: '',
        roomId: '',
    };
    rules = {
        className: [
            { trigger: 'blur', validator: this.nameValid },
            { required: true, message: '请输入课堂名称', trigger: 'blur' },
        ],
        teacher: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { trigger: 'blur', validator: this.nameValid },
        ],
        password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { trigger: 'blur', validator: this.characterValid },
        ],
    };
    mounted() {}
    @Watch('roomDialogEdit.show')
    watchBoolean(val: boolean) {
        if (val === true) {
            this.roomForm.className = this.roomDialogEditData.room_name;
            this.roomForm.teacher = this.roomDialogEditData.anchor_name;
            this.roomForm.password = this.roomDialogEditData.room_key;
            this.roomForm.startTime = this.roomDialogEditData.start_time;
            this.roomForm.roomId = this.roomDialogEditData.room_id;
        }
    }
    // method
    public eidtRoomBtn(roomForm: any) {
        const el: any = this.$refs[roomForm];
        el.validate((valiRes: any) => {
            if (valiRes) {
                let editData = {};
                this.roomDialogEdit.show = false;
                editData = Object.assign({}, this.roomForm);
                this.$emit('updata', editData);
            }
        });
    }

    private nameValid(rule: any, value: string, callback: (err?: Error) => void) {
        if (value) {
            if (!this.checkSpeciaKey(value)) {
                callback(new Error('仅支持输入中英文、数字。'));
            } else {
                callback();
            }
        } else {
            callback();
        }
    }

    private characterValid(rule: any, value: string, callback: (err?: Error) => void) {
        if (value) {
            if (!this.checkCharacter(value)) {
                callback(new Error('密码不支持中文'));
            } else {
                callback();
            }
        } else {
            callback();
        }
    }

    checkSpeciaKey(str: string) {
        let specialKey = "[`~!@#$^&*()=|{}':;',\\[\\].<>/?~！@#￥……&*（）——|{}【】‘；：”“'。，、？@";
        for (let i = 0; i < str.length; i++) {
            if (specialKey.indexOf(str.substr(i, 1)) != -1) {
                return false;
            }
        }
        return true;
    }

    checkCharacter(str: string) {
        let re = new RegExp('[\\u4E00-\\u9FFF]+', 'g');
        if (re.test(str)) {
            return false;
        }
        return true;
    }
}
